import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import { TrackballControls } from 'three/examples/jsm/controls/TrackballControls';
import * as SkeletonUtils from 'three/examples/jsm/utils/SkeletonUtils';

//renderizaje y creacion de camara y escena
const renderer = new THREE.WebGL1Renderer();
renderer.shadowMap.enabled = true;
renderer.setSize(600, 800);
document.getElementById('render3').appendChild(renderer.domElement);
const scene = new THREE.Scene();
const camera = new THREE.PerspectiveCamera(100, 600 / 800, 0.1, 1000);
camera.position.set(3, 2, 3);

//grilla
const gridH = new THREE.GridHelper(4, 4);
scene.add(gridH);

//control de orbita
const controls1 = new OrbitControls(camera, renderer.domElement);
controls1.enabledDamping = true;
controls1.dampingFactor = 0.12;
controls1.enabledZoom = false;
controls1.enablePan = true;
//control de trackball
const controls2 = new TrackballControls(camera, renderer.domElement);
controls2.noRotate = true;
controls2.noPan = true;
controls2.noZoom = false;
controls2.zoomSpeed = 1.5;
camera.position.set(7, 2, 7);

//luz
const dLight = new THREE.SpotLight('#FFF');
scene.add(dLight);
dLight.position.set(0, 100, 100);
dLight.castShadow = true;
dLight.angle = 0.2;
dLight.intensity = 100000;
dLight.penumbra = 2;

//fondo
const color = new THREE.Color('#151F30');
renderer.setClearColor(color);

//plano
const planeGeo = new THREE.PlaneGeometry(8, 8);
const planeMat = new THREE.MeshStandardMaterial({
  color: '#fff',
  side: THREE.DoubleSide,
});
const plane = new THREE.Mesh(planeGeo, planeMat);
scene.add(plane);
plane.rotation.x = -Math.PI / 2;
plane.receiveShadow = true;

//importacion de modelo 3D
const loader = new GLTFLoader();

loader.load('./assets/door.glb', function (glb) {
  const model = glb.scene;
  scene.add(model);
  model.scale.set(0.004, 0.004, 0.004);
  model.position.x = -4;

  const modelClone = SkeletonUtils.clone(model);
  scene.add(modelClone);
  model.position.x = 4;

  const modelClone2 = SkeletonUtils.clone(model);
  scene.add(modelClone2);
  model.position.set(0, 0, 4);
  model.rotation.y = -Math.PI / 2;

  const modelClone3 = SkeletonUtils.clone(model);
  scene.add(modelClone3);
  model.position.set(0, 0, -4);
  model.rotation.y = Math.PI / 2;
});

let mixer1;
let model1;

loader.load('./assets/Orc.gltf', function (gltf) {
  model1 = gltf.scene;
  scene.add(model1);
  model1.scale.set(0.6, 0.6, 0.6);
  model1.position.x = 6;
  model1.rotation.y = -Math.PI / 2;

  const animations = gltf.animations;
  mixer1 = new THREE.AnimationMixer(model1);
  const clip = THREE.AnimationClip.findByName(animations, 'Run');
  const action = mixer1.clipAction(clip);
  action.play();

  model1.traverse(function (node) {
    if (node.isMesh) node.castShadow = true;
    node.receiveShadow = true;
    node.renderOrder = 2;
  });
});

let mixer2;
let model2;
loader.load('./assets/Chicken.gltf', function (gltf) {
  model2 = gltf.scene;
  scene.add(model2);
  model2.scale.set(0.4, 0.4, 0.4);
  model2.position.x = 0;
  model2.rotation.y = -Math.PI / 2;

  const animations = gltf.animations;
  mixer2 = new THREE.AnimationMixer(model2);
  const clip = THREE.AnimationClip.findByName(animations, 'Walk');
  const action = mixer2.clipAction(clip);
  action.play();

  model2.traverse(function (node) {
    if (node.isMesh) node.castShadow = true;
    node.receiveShadow = true;
    node.renderOrder = 2;
  });
});

let mixer3;
let model3;
loader.load('./assets/Velociraptor.glb', function (gltf) {
  model3 = gltf.scene;
  scene.add(model3);
  model3.scale.set(0.4, 0.4, 0.4);
  model3.position.x = -8;
  model3.rotation.y = -Math.PI / 2;

  const animations = gltf.animations;
  mixer3 = new THREE.AnimationMixer(model3);
  const clip = THREE.AnimationClip.findByName(animations, 'Velociraptor_Run');
  const action = mixer3.clipAction(clip);
  action.play();

  model3.traverse(function (node) {
    if (node.isMesh) node.castShadow = true;
    node.receiveShadow = true;
    node.renderOrder = 2;
  });
});

const a = new THREE.Mesh(
  new THREE.BoxGeometry(8, 4, 2.2),
  new THREE.MeshBasicMaterial()
);
scene.add(a);
a.position.set(-8.09, 2, 0);
a.material.colorWrite = false;
a.renderOrder = 1;

const b = a.clone();
scene.add(b);
b.position.set(8.09, 2, 0);
b.renderOrder = 1;

const clock = new THREE.Clock();

function Animate() {
  controls1.update();
  controls2.update();

  if (mixer1 && mixer2 && mixer3) {
    const delta = clock.getDelta();

    mixer1.update(delta);
    mixer2.update(delta);
    mixer3.update(delta);

    model1.position.x -= 0.03;
    model2.position.x -= 0.03;
    model3.position.x -= 0.03;

    if (model1.position.x < -10) model1.position.x = 10;
    if (model2.position.x < -10) model2.position.x = 10;
    if (model3.position.x < -10) model3.position.x = 10;
  }

  renderer.render(scene, camera);
}

renderer.setAnimationLoop(Animate);

window.addEventListener('resize', function () {
  camera.aspect = window.innerWidth / window.innerHeight;
  camera.updateProjectionMatrix();
  renderer.setSize(window.innerWidth, window.innerHeight);
});
